.clearfix {
  zoom: 1;
}
.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}
.nav {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  width: 100%;
  z-index: 22222;
  zoom: 1;
}
.nav:before,
.nav:after {
  content: "";
  display: table;
}
.nav:after {
  clear: both;
}
.nav__default {
  background-color: #33353a;
}
.nav__default .nav__wrap {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.nav__default .nav__wrap__right {
  flex: 1 1 60%;
  justify-content: flex-end;
}
.nav__default .nav__wrap__right .navs .nav_list:last-child {
  margin-right: 0;
}
.nav__default .nav__wrap__right .dt {
  text-align: right;
}
.nav__default .nav__wrap__left {
  flex: 1 1 40%;
}
.nav__default .nav__wrap__left .navs .nav_list:first-child {
  margin-left: 0;
}
.nav__default .nav__wrap__left,
.nav__default .nav__wrap__right {
  display: flex;
  flex-wrap: wrap;
}
.nav__default .nav__wrap__left .navs,
.nav__default .nav__wrap__right .navs {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
  align-items: center;
}
.nav__default .nav__wrap__left .navs .nav_list,
.nav__default .nav__wrap__right .navs .nav_list {
  position: relative;
  margin: 0 1em;
}
.nav__default .nav__wrap__left .navs .nav_list .link,
.nav__default .nav__wrap__right .navs .nav_list .link {
  cursor: pointer;
  padding: 0.5em 1em;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
}
.nav__default .nav__wrap__left .navs .nav_list .link.fill,
.nav__default .nav__wrap__right .navs .nav_list .link.fill {
  background-color: #f8192c;
}
.nav__default .nav__wrap__left .navs .nav_list .link.fill .ic svg path,
.nav__default .nav__wrap__right .navs .nav_list .link.fill .ic svg path {
  fill: #ffffff;
}
.nav__default .nav__wrap__left .navs .nav_list .link .ic,
.nav__default .nav__wrap__right .navs .nav_list .link .ic {
  padding: 0.5em;
  font-size: 1.275em;
}
.nav__default .nav__wrap__left .navs .nav_list .link .ic svg path,
.nav__default .nav__wrap__right .navs .nav_list .link .ic svg path {
  fill: #f8192c;
}
.nav__default .nav__wrap__left .navs .nav_list .link .ic.circle-bg,
.nav__default .nav__wrap__right .navs .nav_list .link .ic.circle-bg {
  height: 46px;
  width: 46px;
  border-radius: 50%;
  -moz-background-clip: padding;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
  background-color: #f8192c;
}
.nav__default .nav__wrap__left .navs .nav_list .link .ic.circle-bg svg path,
.nav__default .nav__wrap__right .navs .nav_list .link .ic.circle-bg svg path {
  fill: #ffffff;
}
.nav__default .nav__wrap__left .navs .nav_list .link .dt,
.nav__default .nav__wrap__right .navs .nav_list .link .dt {
  padding-left: 0.5em;
  position: relative;
}
.nav__default .nav__wrap__left .navs .nav_list .link .dt > span,
.nav__default .nav__wrap__right .navs .nav_list .link .dt > span {
  color: #f8192c;
  display: block;
  margin-bottom: 0.33333333em;
  font-size: 0.78em;
}
.nav__default .nav__wrap__left .navs .nav_list .link .dt > b,
.nav__default .nav__wrap__right .navs .nav_list .link .dt > b {
  color: #ffffff;
}
.nav__default .nav__wrap__left .navs .nav_list .link .dt .nv-btn,
.nav__default .nav__wrap__right .navs .nav_list .link .dt .nv-btn {
  position: relative;
  border-radius: 4em;
  -moz-background-clip: padding;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
  border: 1px solid #ffffff;
  padding: 0.5em 1em;
  color: #ffffff;
}
.nav__default .nav__wrap__left .navs .nav_list .link .dt.cart > b > span,
.nav__default .nav__wrap__right .navs .nav_list .link .dt.cart > b > span {
  display: inline-block;
}
.nav__default .nav__wrap__left .navs .nav_list .link .dt.cart > b > span:last-child,
.nav__default .nav__wrap__right .navs .nav_list .link .dt.cart > b > span:last-child {
  margin-left: 0.5em;
}
@media screen and (max-width: 991px) {
  .nav__default .nav__wrap .res {
    display: none;
  }
}
.nav__inline {
  background-color: #ffffff;
  zoom: 1;
  position: relative;
}
.nav__inline:before,
.nav__inline:after {
  content: "";
  display: table;
}
.nav__inline:after {
  clear: both;
}
.nav__inline_wrap {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  list-style: none;
  margin: 0;
  padding: 0;
}
.nav__inline_wrap .link {
  flex: 1 1;
}
.nav__inline_wrap .link > a,
.nav__inline_wrap .link > div,
.nav__inline_wrap .link > span {
  -webkit-transition: all 200ms ease-in-out;
  -moz-transition: all 200ms ease-in-out;
  -ms-transition: all 200ms ease-in-out;
  -o-transition: all 200ms ease-in-out;
  text-align: center;
  display: block;
  text-decoration: none;
  color: #808080;
  padding: 1em;
}
.nav__inline_wrap .link > a.active,
.nav__inline_wrap .link > div.active,
.nav__inline_wrap .link > span.active {
  background-color: #f8192c;
  color: #ffffff;
}
.nav__inline_wrap .link > a.active:hover,
.nav__inline_wrap .link > div.active:hover,
.nav__inline_wrap .link > span.active:hover {
  color: #ffffff;
}
.nav__inline_wrap .link > a:hover,
.nav__inline_wrap .link > div:hover,
.nav__inline_wrap .link > span:hover {
  color: #f8192c;
}
