.clearfix {
  zoom: 1;
}
.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}
body {
  font-family: 'Roboto', sans-serif;
  background-color: #161719;
  color: #808185;
  font-size: 0.85em;
}
* {
  box-sizing: border-box;
}
.uppercase {
  text-transform: uppercase;
}
